.btn-circle {
    padding: 15px 25px;
    font-size: 18px;
    text-align: center;
    width: 150px;
    height: 150px;
    cursor: pointer;
    outline: none;
    border-radius: 1000px;
    color: #fff;
    border: none;
    box-shadow: 0 9px #999;
}

.btn-circle:active {
    background-color: #858585;
    box-shadow: 0 5px #666;
    transform: translateY(4px);
}

